html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}
html {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
snap-content,
snap-drawer {
  padding-top: 20px;
}
body {
  font-family: 'Ubuntu', sans-serif !important;
  background-color: #333;
}
.snap-content {
  background: #4b6a7a;
  background-image: -webkit-radial-gradient(ellipse at center,rgba(0,183,234,0.7) 0%,#54778a 100%);
  background-image: -moz-radial-gradient(ellipse at center,rgba(0,183,234,0.7) 0%,#54778a 100%);
  background-image: -ms-radial-gradient(ellipse at center,rgba(0,183,234,0.7) 0%,#54778a 100%);
  background-image: -o-radial-gradient(ellipse at center,rgba(0,183,234,0.7) 0%,#54778a 100%);
  background-image: radial-gradient(ellipse at center,rgba(0,183,234,0.7) 0%,#54778a 100%);
}
h1 {
  color: #fff;
  font-size: 24px;
}
.offcanvas {
  background-color: #333;
}
.tm-navbar {
  background: transparent !important;
  border: none;
}
.tm-navbar .uk-navbar-toggle {
  font-size: 24px;
  color: #fff;
}
